.car {
  /* Add shadows to create the "card" effect */
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 1rem;
  background-color: #fff;
  align-items: center;
  color: black;
  margin: 20px;
  display: flex;
  flex-direction: row;
}

/* On mouse-over, add a deeper shadow */
.car:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

/* Add some padding inside the card container */
.container {
  padding: 2px 16px;
}

.quote {
  font-size: 1.8rem;
}

.author {
  text-align: end;
  margin-right: 40px;
  margin-top: 20px;
  font-size: 1.2rem;
  color: #666;
}
.quotesimg {
  width: 200px;
  height: 200px;
  border-radius: 0.5rem;
}

@media (max-width: 600px) {
  .car {
    /* Add shadows to create the "card" effect */
    height: auto;
    margin: 5px;
    display: flex;
    flex-direction: row;
    margin-bottom: 20px;
  }
  .quote {
    font-size: 1rem;
  }

  .quotesimg {
    width: 100px;
    height: 100px;
  }
  .author {
    font-size: 0.5rem;
  }
}
